import React, {
  useCallback, useEffect, useMemo, useState
} from 'react';
import { useTranslation } from 'react-i18next';
import {
  Box, Button, FormControl, FormLabel, Input, useStyleConfig,
} from '@chakra-ui/react';
import { shallowEqual } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import MiniBox from '../../../../layout/MiniBox';
import { useAppAsyncDispatch, useAppSelector } from '../../../../hooks/store';
import { updateSelectedBusinessOwner } from '../../../../store/actionCreators/business';
import AddressSearch from '../../../../components/AddressSearch';

/**
 * Kyb Owner Address - Component for collecting the adress details of the bussiness owners.
 * @module KYB
 * @returns {JSX.Element}
 */
const BusinessOwnerAddress = () => {
  const { t } = useTranslation();
  const navigate = useNavigate();
  const dispatch = useAppAsyncDispatch();

  const [formData, updateFormData] = useState({
    line1: '',
    line2: '',
    city: '',
    state: '',
    country: '',
    postalCode: '',
    addressType: 'legal'
  });

  const style:any = useStyleConfig('FormControl', {
    variant: 'primary'
  });

  const selectedBusinessOwner = useAppSelector((store) => store.business.selectedBusinessOwner, shallowEqual);

  const isValid = useMemo(() => (
    !!formData.line1
      && !!formData.city
      && !!formData.state
      && !!formData.country
      && !!formData.postalCode
      && formData.line1.length !== 0
      && formData.city.length !== 0
      && formData.state.length !== 0
      && formData.country.length !== 0
      && formData.postalCode.length !== 0
  ), [formData]);

  const submit = useCallback(() => {
    if (selectedBusinessOwner) {
      dispatch(updateSelectedBusinessOwner({
        ...selectedBusinessOwner,
        person: {
          ...selectedBusinessOwner.person,
          address: formData
        }
      }));
      navigate('/ao/kyb/ownerOwnership');
    }
  }, [formData, selectedBusinessOwner]);

  useEffect(() => {
    if (selectedBusinessOwner?.person.address) {
      updateFormData({ ...selectedBusinessOwner?.person.address });
    }
  }, [selectedBusinessOwner]);
  return (
    <div className="page">
      <form onSubmit={(e) => {
        e.preventDefault();
        submit();
      }}
      >
        <MiniBox
          varient="box"
          heading={t('person_address.title')}
          footer={(
            <Button data-testid="submit-btn" width="100%" variant="primary" type="submit" disabled={!isValid}>
              {t('person_address.ctaText')}
            </Button>
)}
        >
          <Box width="100%">
            <FormControl variant="primary" style={style}>
              <FormLabel variant="primary">
                {t('person_address.address1')}
              </FormLabel>
              <AddressSearch
                googleApiKey={process.env.REACT_APP_GMAP_KEY || ''}
                placeholder={t('person_address.address1')}
                address={formData}
                onChange={(address) => {
                  updateFormData({
                    ...address,
                    addressType: 'legal',
                  });
                }}
              />
            </FormControl>

            <FormControl variant="primary" style={style}>
              <FormLabel variant="primary">
                {t('person_address.address2')}
              </FormLabel>
              <Input
                data-testid="address-line2"
                placeholder={t('person_address.address2')}
                variant="primary"
                value={formData.line2}
                onChange={(e) => updateFormData({
                  ...formData,
                  line2: e.target.value
                })}
              />
            </FormControl>

            <FormControl variant="primary" style={style}>
              <FormLabel variant="primary">
                {t('person_address.addressCity')}
              </FormLabel>
              <Input
                data-testid="address-city"
                placeholder={t('person_address.addressCity')}
                variant="primary"
                value={formData.city}
                onChange={(e) => updateFormData({
                  ...formData,
                  city: e.target.value
                })}
              />
            </FormControl>

            <FormControl variant="primary" style={style}>
              <FormLabel variant="primary">
                {t('person_address.addressState')}
              </FormLabel>
              <Input
                data-testid="address-state"
                placeholder={t('person_address.addressState')}
                variant="primary"
                value={formData.state}
                onChange={(e) => updateFormData({
                  ...formData,
                  state: e.target.value
                })}
              />
            </FormControl>

            <FormControl variant="primary" style={style}>
              <FormLabel variant="primary">
                {t('person_address.addressZipcode')}
              </FormLabel>
              <Input
                data-testid="address-zipcode"
                placeholder={t('person_address.addressZipcode')}
                variant="primary"
                value={formData.postalCode}
                onChange={(e) => updateFormData({
                  ...formData,
                  postalCode: e.target.value
                })}
              />
            </FormControl>
          </Box>
        </MiniBox>
      </form>
    </div>
  );
};

export default BusinessOwnerAddress;
